/// <reference path="../../vendor_def/tsd.d.ts" />

module sb {
  'use strict';

  export interface EnumDef {
    name: string
    code: string
  }
  export interface Enums {
    userStatus: {
      ENA: string
      DIS: string
      REG: string
    }
    userRole: {
      ADM: EnumDef
      REC: EnumDef
      INT: EnumDef
    }
    jobStatus: {
      PUB: EnumDef
      RDY: EnumDef
      ARC: EnumDef
    }
    applicationSource: {
      DRC: EnumDef
      MNL: EnumDef
      JSE: EnumDef
    }
    indexingStatus: {
      NVR: EnumDef
      PUB: EnumDef
      RJC: EnumDef
    }
    jobType: any
    salaryUnit: any[]
    salaryUnitEnum: {
      Hourly: EnumDef
      Daily: EnumDef
      Monthly: EnumDef
      Yearly: EnumDef
      Negotiable: EnumDef
      NotOpen: EnumDef
    }
    selectionStageOptions: any[]
    imageFileType: string[]
    resumeFileType: string[]
    timelineType: {
      stage: string
      note: string
      interview: string
      message: string
      feedback: string
      docscreening: string
    }
    feedbackOptions: any[]
    jobApplicableStatus: {
      OPN: EnumDef
      CLS: EnumDef
      OTH: EnumDef
    }
    interviewType: any;
    interviewFeedbackGrade: any[]
  }

  export class StaticConfig {
    images = {
      maxFileSize: 5 * 1024 * 1024,

      logo: {
        minWidth: 50,
        minHeight: 50
      },

      cover: {
        minWidth: 500,
        minHeight: 200
      },

      inline: {
        minWidth: 300,
        minHeight: 200
      }
    }

    applications = {
      attachments: {
        maxFileSize: 5 * 1024 * 1024
      }
    }

    terms = {
      max: 30,
      termsLength: 50,
      descLength: 200
    }
    
    locations = {
      max: 10
    }
  }
}

